import { Router } from '@angular/router';
import { VhAuth, VhQuerySales, VhQueryDealer } from 'ionic-vhframeworks';
import { Component, OnInit } from '@angular/core';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';

@Component({
  selector: 'app-payment',
  templateUrl: './payment.component.html',
  styleUrls: ['./payment.component.scss']
})
export class PaymentComponent implements OnInit {

  constructor(
    private vhQuerySales: VhQuerySales,
	private vhQueryDealer: VhQueryDealer,	
    private vhAuth: VhAuth,
    private router: Router,
    private vhComponent: VhComponent
  ) { }

  ngOnInit(): void {

    // go to thanh toán
    this.vhQueryDealer.getPayments(
      { id_dealer: { $eq: this.vhAuth.getUser()._id }, payment_type: { $eq: 5 } },
      {},
      {},
      0
    ).then((payments: any) => {
      if (payments) {
        if (payments.length > 0) {
          this.router.navigate(['/dealer/dashboard/payment/detail'],
            {
              state: payments
            });
        } else this.vhComponent.hideLoading(0)
      } else this.vhComponent.hideLoading(0)
    }, err => {
      console.log(err);

    })


  }

}
