import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhQuerySales, VhQueryDealer } from 'ionic-vhframeworks';
import { NzModalService } from 'ng-zorro-antd/modal';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'atw-qr-banking-modal',
  templateUrl: './qr-banking-modal.component.html',
  styleUrls: ['./qr-banking-modal.component.scss']
})
export class QrBankingModalComponent implements OnInit {
  @Input() total = 5000;
  @Input() id_payment = ''
  banks: any = []
  urlImg: string = 'https://img.vietqr.io/image/<BANK_ID>-<ACCOUNT_NO>-<TEMPLATE>.png?amount=<AMOUNT>&addInfo=<DESCRIPTION>&accountName=<ACCOUNT_NAME>'
  loading: boolean = false;
  loading_banks: boolean = false;
  bankAccounts: any[] = [];
  bank_active = {}
  constructor(
    private vhQuerySales: VhQuerySales,
	private vhQueryDealer: VhQueryDealer,	
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private router: Router,
    private nzModalService:NzModalService
  ) { }

  ngOnInit() {

    this.getBanks()
  }

  getBanks() {
    this.vhQueryDealer.vietqr_getBanks()
      .then((res: any) => {
        // console.log('vietqr_getBanks', res)
        if (res.vcode == 0) {
          this.banks = res.data
          this.getBanksAccount()
        }
      })
      .catch((err) => console.error(err))

  }
  interval: any
  ngAfterViewInit(): void {
    this.interval = setInterval(() => {
      this.vhQueryDealer.getPayment(this.id_payment)
        .then((payment: any) => {
          console.log(payment);
          
          if (payment.payment_type == 1 && payment.paid_online) {
            this.vhComponent.alertMessageDesktop("success", this.languageService.translate("Thanh toán thành công"))
            this.nzModalService.closeAll()
            this.router.navigate(['/dealer/dashboard/payment']);
            clearInterval(this.interval)
          }
        })
    }, 5000);

  }

  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    clearInterval(this.interval)
  }

  getBanksAccount() {
    this.bankAccounts = [
      // {
      //   _id: "1",
      //   acq_id: "970436",
      //   account_name: "CÔNG TY TNHH GIẢI PHÁP CÔNG NGHỆ VIETHAS",
      //   account_no: "0441000644964",
      //   template: "compact2",
      //   // branch: this.vhQuerySales.getDefaultBranch()._id
      // },
      {
        _id: "7",
        acq_id: "970422",
        account_name: "LE DUC HUY",
        account_no: "0909925354",
        template: "compact2",
        // branch: this.vhQuerySales.getDefaultBranch()._id
      },
      // {
      //   _id: "2",
      //   acq_id: "970436",
      //   account_name: "Nguyễn Hoàng Nam",
      //   account_no: "0251002660414",
      //   template: "compact2",
      //   // branch: this.vhQuerySales.getDefaultBranch()._id
      // },
      // {
      //   _id: "3",
      //   acq_id: "970406",
      //   account_name: "Nguyễn Hoàng Nam",
      //   account_no: "0104782569",
      //   template: "compact2",
      //   // branch: this.vhQuerySales.getDefaultBranch()._id
      // },
      // {
      //   _id: "4",
      //   acq_id: "970403",
      //   account_name: "Nguyễn Hoàng Nam",
      //   account_no: "060007797128",
      //   template: "compact2",
      //   // branch: this.vhQuerySales.getDefaultBranch()._id
      // },
      // {
      //   _id: "5",
      //   acq_id: "970416",
      //   account_name: "Nguyễn Hoàng Nam",
      //   account_no: "115621719",
      //   template: "compact2",
      //   // branch: this.vhQuerySales.getDefaultBranch()._id
      // },
      // {
      //   _id: "6",
      //   acq_id: "970405",
      //   account_name: "Nguyễn Hoàng Nam",
      //   account_no: "6360205227977",
      //   template: "compact2",
      //   // branch: this.vhQuerySales.getDefaultBranch()._id
      // },
    ];

    this.bankAccounts.forEach((bank) => {
      bank.logo = this.banks.find((b) => b.bin == bank.acq_id)?.logo
    })

    if (this.bankAccounts.length) {
      this.handleClick(this.bankAccounts[0])
    }
  }

  handleClick(bank) {
    this.bank_active = bank
    this.loading = true
    const accountNo = bank.account_no;
    const accountName = bank.account_name;
    const acqId = bank.acq_id;
    const template = bank.template;
    const amount = this.total;
    const idBill = this.id_payment;
    this.vhQueryDealer.vietqr_generateQR(accountNo, accountName, acqId, template, idBill, amount)
    // this.vhQueryDealer.vietqr_generateQR('0909925354', 'LE DUC HUY', '970422', 'compact2', this.id_payment, 2000)
      .then((res: any) => {
        console.log('vietqr_generateQR', res)
        if (res.vcode == 0) {
          this.urlImg = res.data
        }
      })
      .catch((err) => console.error(err))
      .finally(() => this.loading = false)
  }
}
